___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 36px;
  border-radius: 10px;
  text-align: center;
  align-items: center;
  gap: 12px;
}

form input, form label, form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
}

form button:hover {
  background: var(--primary-color-darker);
}

h1 {
  padding: 0;
}

.cpf-gerado {
  font-size: 52px;
  text-align: center;
  color: var(--primary-color);
  margin-bottom: 12px;
}

button {
  color: white;
  background-color: var(--primary-color);
  border-radius: 8px;
  height: 44px;
  padding: 8px 12px;
  outline: none;
  font-size: 16px;
  border: none;
  font-weight: 500;
  min-width: 100px;
}

button:hover {
  cursor: pointer;
